@import url(https://fonts.googleapis.com/css2?family=Roboto&family=Ubuntu:wght@700&display=swap);
:root {
  --primary-color: #1DB954;
  --title-color: #011627;
  --text-color: #6C6C80;
}

* {
  margin: 0;
  padding: 0;
  box-sizing: border-box;
}

body {
  background: #F0F0F5;
  -webkit-font-smoothing: antialiased;
  color: #6C6C80;
  color: var(--text-color);
}

body, input, button {
  font-family: Roboto, Arial, Helvetica, sans-serif;
}

h1, h2, h3, h4, h5, h6 {
  color: #011627;
  color: var(--title-color);
  font-family: Ubuntu;
}

#page-home {
    height: 100vh;
    background: url(/static/media/home-background.849cb0c5.svg) no-repeat 40vw top;
  }

  #page-home .content {
    width: 100%;
    height: 100%;
    max-width: 1100px;
    margin: 0 auto;
    padding: 0 30px;
  
    display: flex;
    flex-direction: column;
    align-items: flex-start;
  }
  
  #page-home .content header {
    margin: 48px 0 0;
  }
  
  #page-home .content main {
    flex: 1 1;
    max-width: 560px;
  
    display: flex;
    flex-direction: column;
    justify-content: center;
  }
  
  #page-home .content main h1 {
    font-size: 54px;
    color: var(--primary-color);
  }
  
  #page-home .content main p {
    font-size: 24px;
    margin-top: 24px;
    line-height: 38px;
  }
  
  #page-home .content main a {
    width: 100%;
    max-width: 360px;
    height: 72px;
    background: var(--primary-color);
    border-radius: 8px;
    text-decoration: none;
  
    display: flex;
    align-items: center;
    overflow: hidden;
  
    margin-top: 40px;
  }
  
  #page-home .content main a span {
    display: block;
    background: rgba(0, 0, 0, 0.08);
    width: 72px;
    height: 72px;
  
    display: flex;
    align-items: center;
    justify-content: center;
    transition: background-color 0.2s;
  }
  
  #page-home .content main a span svg {
    color: #FFF;
    width: 20px;
    height: 20px;
  }
  
  #page-home .content main a strong {
    flex: 1 1;
    text-align: center;
    color: #FFF;
  }
  
  #page-home .content main a:hover {
    background: #2FB86E;
  }
  
  @media(max-width: 900px) {
    #page-home .content {
      align-items: center;
      text-align: center;
    }
  
    #page-home .content header {
      margin: 48px auto 0;
    }
  
    #page-home .content main {
      align-items: center;
    }
  
    #page-home .content main h1 {
      font-size: 42px;
    }
  
    #page-home .content main p {
      font-size: 24px;
    }
  }
  

  @media only screen and (max-width: 768px) {
    #page-home {
      background: url(/static/media/home-background.849cb0c5.svg) no-repeat 11vw top;
      background-size: 90vw;
}
    
    #page-home .content header {
      margin: 48vh 0 0;
    }

    #page-home .content main {
      max-width: 500px;
      margin-top: 36px;
      display: block;
      flex-direction: column;
      justify-content: center;
    }

    #page-home .content main h1 {
      font-size: 38px;
    }
  
    #page-home .content main p {
      font-size: 20px;
    }
  }


  
